.name-container-user {
    position: relative;
    display: inline-block;
    /* Ensures the title and menu are grouped together */
}

.role-container-user {
    position: relative;
    display: inline-block;
    /* Ensures the title and menu are grouped together */
}

.username-title {
    cursor: pointer;
    position: relative;
    /* Keeps it positioned as part of the flow */
}

.role-title {
    cursor: pointer;
    position: relative;
    /* Keeps it positioned as part of the flow */
}

.name-menu-user {
    position: absolute;
    top: 90px;
    /* Adjust to make it appear above the table row */
    left: 0;
    transform: translateY(-100%);
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    padding: 10px;
    z-index: 9999;
    width: 200px;
    pointer-events: auto;
    /* Ensures it can be interacted with */
}

.role-menu-user {
    position: absolute;
    top: 90px;
    /* Adjust to make it appear above the table row */
    left: 0;
    transform: translateY(-100%);
    background-color: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    padding: 10px;
    z-index: 9999;
    width: 200px;
    pointer-events: auto;
    /* Ensures it can be interacted with */
}

.filter-input {
    width: calc(100% - 20px);
    padding: 10px;
    border-radius: 8px;
    /* Rounded corners */
    border: 1px solid #ddd;
    /* Light gray border */
    font-size: 14px;
    outline: none;
    transition: all 0.3s ease;
    /* Smooth transition on focus */
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    /* Subtle shadow for a soft effect */
}

.filter-input:focus {
    border-color: #4CAF50;
    /* Change border color on focus */
    box-shadow: 0px 2px 8px rgba(0, 128, 0, 0.2);
    /* Glow effect on focus */
    background-color: #f9f9f9;
    /* Slight background color change */
}

.filter-input::placeholder {
    color: #888;
    /* Lighter color for the placeholder */
}